@import "sass/index";

.banner {
  position: relative;
  padding: 0 !important;
  margin: 0 spacing(1) !important;
  background: none;

  &:hover,
  &:focus,
  &:active {
    background: none;
  }
}

.bannerTextWrapper {
  flip: false;
  position: absolute;
  top: 0;
  left: 0;
  width: 90px;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 5px;
}
.image {
  background-size: 100%;
}
.skeleton {
  transition: opacity $defaultTransition;
}
.easing {
  opacity: 1;
}
.dimensions {
}
